.InputSection_container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

input, textarea {
  min-width: 800px;
  border: solid 1px gray;
  border-radius: 0.3em;
  padding: 0.5em;    
  margin: 0.5em;
  font-size: 10pt;
}  

  .InputSection_container_btnWrapper {
    display: flex;
    flex-direction: row;
    margin-top: 0.5em;
}
    .InputSection_container_btnWrapper .button {
      background-color: black;
      border: none;
      color: white;
      font-size: 10pt;
      border-radius: 0.3em;
      padding: 0.5em;
    
  }
